<template>
  <div style="height: 738px;width: 100%"></div>
</template>

<script>
export default {
  name: "ScatterChart",
  data() {
    return {
      xData: this.$store.state.xAxis, //
      yData: this.$store.state.yData,  //
      scatterData: this.$store.state.allStatisticData.scatterData,  //
    };
  },
  mounted() {
    this.initEcharts();
  },
  methods: {
    initEcharts() {
      // 
      const option = {
        title: this.$store.state.tittle,
        tooltip: {
          trigger: 'item'
        },
        xAxis: {
          type: 'value',
          scale: true
        },
        yAxis: {
          type: 'value',
          scale: true
        },
        series: [
          {
            symbolSize: 20,
            data: this.scatterData,
            type: 'scatter',
            markLine : {
              symbol: ['none'], //['none']['arrow', 'none']['none','arrow']
              label:{
                position:"start" //start,"middle","end"   
              },
              data : [{
                silent:false, // truefalse
                lineStyle:{ //  
                  type:"solid", //  solid'dotted'
                  color:"#FA3934",
                  width: 3   //
                },
                yAxis: 4 // yAxis,  {type : 'average', name: ''}type max min average
              }]
            },
          },
        ]
      };
      const myChart = this.$echarts.init(this.$el);
      this.scatterChart=myChart
      this.scatterChartOption = option
      myChart.clear();
      myChart.resize()
      myChart.setOption(option);
      //
      window.addEventListener("resize", () => {
        myChart.resize();
      });
    }
  },
  watch:{
    xData:{
      handler:function () {
        console.log('computedXData')
        this.scatterChart.setOption(this.scatterChartOption);
      },
    }
  }
}
</script>

