<template>
  <div>
    <div style="height: 300px;margin-top: 50px;float: left" v-show="!$store.state.user">
      <div style="font-size: 50px"></div>
      <span></span><br/>
      <input type="text" v-model="inputUserCode" placeholder=""
                style="margin-bottom: 50px;margin-top: 50px"/><br/>
      <input type="password" v-model="inputUserPassword" placeholder="" style="margin-bottom: 50px"/><br/>
      <el-button @click="loginWeb"></el-button>
      <el-button @click="registerWeb"></el-button>
    </div>
    <div style="margin-top: 80px;float: left" v-show="$store.state.user">
      <div style="font-size: 50px"></div>
      <div style="font-size: 50px;margin-bottom: 200px"> - </div>
      <span></span>
      <el-button @click="logoutWeb" style="margin-left: 50px"></el-button><br/>
      <el-button @click="loginJavaFX" style="margin-top: 100px"></el-button>
      <el-button @click="logoutJavaFX"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "LoginRegister",
  data() {
    return {
      inputUserCode: 'admin',
      inputUserPassword: '123456',
    }
  },
  methods: {
    loginJavaFX() {
      this.axios.get('http://localhost:8081/doLogin?userCode=admin&userPassword=123456').then(function (response) {
        console.log(response)
      }).catch(function (error) {
        console.log(error)
      })
    },
    logoutJavaFX() {
      this.axios.get('http://localhost:8081/doLogout').then(function (response) {
        console.log(response)
      }).catch(function (error) {
        console.log(error)
      })
    },
    loginWeb() {
      console.log(this.$store.state.user)
      this.$store.state.user = 1;
      sessionStorage.setItem('user', this.$store.state.user)
    },
    logoutWeb() {
      sessionStorage.removeItem('user')
      this.$store.state.user = null;
    },
    registerWeb() {

    }
  }
}
</script>

