<template>
  <div class="header">
    <h3>TodoList</h3>
    <el-divider></el-divider>
    <el-row :gutter="20">
      <el-col :span="6">
        <el-input
            placeholder=""
            v-model="addTodoList.name"
            clearable>
        </el-input>
      </el-col>
      <el-col :span="6">
        <el-input
            placeholder=""
            v-model="addTodoList.comTimes"
            clearable>
        </el-input>
      </el-col>
      <el-col :span="6">
        <el-input
            placeholder=""
            v-model="addTodoList.tips"
            clearable>
        </el-input>
      </el-col>
      <el-col :span="6">
        <el-date-picker
            v-model="addTodoList.time"
            type="date"
            placeholder=""
            format="yyyy  MM  dd "
            value-format="yyyy-MM-dd">
        </el-date-picker>
      </el-col>
    </el-row>
    <el-button class="add" type="primary" @click="add"></el-button>
    <el-table
        highlight-current-row
        :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
      <el-table-column
          label=""
          width="180">
        <template slot-scope="scope">
          {{ scope.$index + 1 }}
          <input type="checkbox" checked v-model="tableData[scope.$index].isDone"
                 style="object-fit: fill;height: 18px;width: 18px">
        </template>
      </el-table-column>
      <el-table-column
          row-class-name="rowHighLight"
          prop="name"
          label=""
          width="180">
      </el-table-column>
      <el-table-column
          prop="comTimes"
          label=""
          width="180">
      </el-table-column>
      <el-table-column
          prop="tips"
          label=""
          width="240">
      </el-table-column>
      <el-table-column
          label=""
          width="300">
        <template slot-scope="scope">
          <a style="text-decoration:none;" :href="''+tableData[scope.$index].isJump"
             v-show="tableData[scope.$index].isJump" target="_blank">{{ tableData[scope.$index].name }}</a>
        </template>
      </el-table-column>
      <el-table-column
          prop="time"
          label=""
          width="150">
      </el-table-column>
      <el-table-column
          prop="colTime"
          label=""
          width="150">
        <template slot-scope="scope">
          {{
            new Date(tableData[scope.$index].colTime).getFullYear() + '-' +
            (new Date(tableData[scope.$index].colTime).getMonth() + 1) + '-' +
            new Date(tableData[scope.$index].colTime).getDate()
          }}
        </template>
      </el-table-column>
      <el-table-column>
        <template slot="header" slot-scope="scope">
          <el-input
              v-model="search"
              size="mini"
              placeholder="" @click="scope"/>
        </template>
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" @click="edit(scope.row,scope.$index)" circle></el-button>
          <el-button type="danger" icon="el-icon-delete" @click="del(scope.$index)" circle></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
      <div>
        <el-form ref="form" :model="editUser" label-width="40px">
          <el-row>
            <el-form-item>
              <span style="font-size: 24px;float: left">:</span><input v-model="editUser.name"/>
            </el-form-item>
          </el-row>
          <el-form-item>
            <span style="font-size: 24px;float: left">:</span><input v-model="editUser.comTimes"/>
          </el-form-item>
          <el-form-item>
            <span style="font-size: 24px;float: left">:</span><input v-model="editUser.tips"/>
          </el-form-item>
          <el-form-item>
            <span style="font-size: 24px;float: left">:</span><input v-model="editUser.isJump"/>
          </el-form-item>
          <el-form-item>
            <span style="font-size: 24px;float: left">:</span>
            <el-date-picker
                style="width: 281px;border-color: black"
                v-model="editUser.time"
                type="date"
                placeholder=""
                format="yyyy  MM  dd "
                value-format="yyyy-MM-dd">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="confirm"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
// import Vue from 'vue'
import {MessageBox} from 'element-ui'

export default {
  name: 'TodoListModule',
  data() {
    return {
      addTodoList: { //
        name: '',
        comTimes: '',
        tips: '',
        isJump: '',
        time: '',
        isDone: false,
        colTime: '',
      },
      tableData: [{
        name: '1',
        comTimes: '1',
        tips: '1',
        isJump: '',
        time: '2023-03-12',
        isDone: true,
        colTime: '2023-03-12',
      }, {
        name: '2',
        comTimes: '2',
        tips: '2',
        isJump: '',
        time: '2023-03-12',
        isDone: false,
        colTime: '2023-03-12',
      }],
      dialogVisible: false, //
      editUser: { //
        name: '',
        comTimes: '',
        tips: '',
        isJump: '',
        time: '',
        isDone: false,
        colTime: '',
      },
      userIndex: 0,
      search: ''
    }
  },
  methods: {
    add() {
      if (!this.addTodoList.name) {
        this.$message({
          message: '',
          type: 'warning'
        });
        return;
      }
      if (!this.addTodoList.comTimes) {
        this.$message({
          message: '',
          type: 'warning'
        });
        return;
      }
      if (!this.addTodoList.time) {
        this.$message({
          message: '',
          type: 'warning'
        });
        return;
      }
      this.addTodoList.colTime = new Date().valueOf()
      this.addTodoList.isDone = false
      this.tableData.push(this.addTodoList)
      this.addTodoList = { //
        name: '',
        comTimes: '',
        tips: '',
        time: '',
        isDone: false,
        colTime: '',
      }
    },
    del(idx) {
      MessageBox.confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.tableData.splice(idx, 1)
        this.$message({
          type: 'success',
          message: '!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    handleClose() {
      this.dialogVisible = false
    },
    edit(item, idx) {
      this.userIndex = idx
      this.editUser = {
        name: item.name,
        comTimes: item.comTimes,
        tips: item.tips,
        isJump: item.isJump,
        time: item.time,
      }
      this.dialogVisible = true
    },
    confirm() {
      this.dialogVisible = false
      //this.tableData[this.userIndex] = this.editUser    JavaScript Vue 
      // Vue.set(this.tableData, this.userIndex, this.editUser)

      this.tableData[this.userIndex].name = this.editUser.name
      this.tableData[this.userIndex].comTimes = this.editUser.comTimes
      this.tableData[this.userIndex].tips = this.editUser.tips
      this.tableData[this.userIndex].isJump = this.editUser.isJump
      this.tableData[this.userIndex].time = this.editUser.time
    },
  },
  computed: {
    conputedColTime(scope) {
      const colDateObj = new Date(this.tableData[scope.$index].colTime)
      return colDateObj.getFullYear() + '-' + colDateObj.getMonth() + '-' + colDateObj.getDate()
    }
  },
  watch: {
    tableData: {
      deep: true,
      handler: function () {
        localStorage.setItem("TodoListData", JSON.stringify(this.tableData))
      }
    }
  },
  mounted() {
    this.tableData = JSON.parse(localStorage.getItem("TodoListData"))
    const colDateTimeStamp = new Date().valueOf();

    this.tableData.forEach(function (item, index, self) {
      index;
      self;
      if (item.colTime === null || item.colTime === undefined) {
        item.colTime = new Date().valueOf();
        console.log('')
      }
      if (new Date(item.colTime).setHours(23, 59, 59, 999) < colDateTimeStamp) {
        item.isDone = false
        item.colTime = new Date().valueOf();
        console.log('')
      }
    })
  }
}
</script>

<style scoped>
h3 {
  margin: 20px;
}

.add {
  margin: 20px 0;
  width: 100%;
}
</style>