<template>
  <div style="height: 738px;width: 100%"></div>
</template>

<script>
export default {
  name: "LineChart",
  data() {
    return {
      xData: this.$store.state.xAxis, //
      yData: this.$store.state.yData, //
    };
  },
  mounted() {
    this.initEcharts();
  },
  methods: {
    initEcharts() {
      // 
      const option = {
        title: this.$store.state.tittle,
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: this.xData
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            data: this.yData,
            type: 'line'
          }
        ]
      };
      const myChart = this.$echarts.init(this.$el);
      this.lineChart=myChart
      this.lineChartOption = option
      myChart.clear();
      myChart.resize()
      myChart.setOption(option);
      //
      window.addEventListener("resize", () => {
        myChart.resize();
      });
    }
  },
  watch:{
    xData:{
      handler:function () {
        console.log('computedXData')
        this.lineChart.setOption(this.lineChartOption);
      },
    }
  }
}
</script>

