<template>
  <div>
    <div style="margin-left: 50px;margin-top: 50px">
      <el-descriptions title="" direction="vertical" :column="4" border>
        <el-descriptions-item label="">kooriookami</el-descriptions-item>
        <el-descriptions-item label="">18100000000</el-descriptions-item>
        <el-descriptions-item label="" :span="2"></el-descriptions-item>
        <el-descriptions-item label="">
          <el-tag size="small"></el-tag>
        </el-descriptions-item>
        <el-descriptions-item label=""> 1188 </el-descriptions-item>
      </el-descriptions>
    </div>
    <el-row style="float: left;margin-left: 50px;margin-top: 50px;margin-bottom: 50px">
      <el-button></el-button>
    </el-row>

    <el-row>
      <div style="margin-right: 1000px;margin-left: 50px;margin-top: 100px">
        <el-descriptions title=""></el-descriptions>
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px"
                 class="demo-ruleForm">
          <el-form-item label="" prop="pass">
            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="checkPass">
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "UserCenter",
  data() {
    const checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error(''));
        } else {
          if (value < 18) {
            callback(new Error('18'));
          } else {
            callback();
          }
        }
      }, 1000);
    };
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        pass: '',
        checkPass: '',
        age: ''
      },
      rules: {
        pass: [
          {validator: validatePass, trigger: 'blur'}
        ],
        checkPass: [
          {validator: validatePass2, trigger: 'blur'}
        ],
        age: [
          {validator: checkAge, trigger: 'blur'}
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

