<template>
  <div style="height: 738px;width: 100%"></div>
</template>

<script>
export default {
  name: "BarChart",
  data() {
    return {
      xData: this.$store.state.xAxis, //
      yData: this.$store.state.yData, //
    };
  },
  mounted() {
    this.initEcharts();
  },
  methods: {
    initEcharts() {
      // 
      const option = {
        title: this.$store.state.tittle,
        tooltip: {
          trigger: 'axis'
        },
        xAxis: [{
          data: this.xData
        },
        ],
        yAxis: {},
        series: [
          {
            type: "bar", //
            showBackground: true,
            data: this.yData
          }
        ]
      };
      const myChart = this.$echarts.init(this.$el);
      this.barChart = myChart
      this.barChartOption = option
      myChart.clear();
      myChart.resize()
      myChart.setOption(option);
      //
      window.addEventListener("resize", () => {
        myChart.resize();
      });
    }
  },
  watch: {
    xData: {
      handler: function () {
        this.barChart.setOption(this.barChartOption);
      },
    }
  }
}
</script>

